import React, { useState } from 'react';
import { Container, Row, Col } from 'reactstrap';
import { useForm } from 'react-hook-form';
import './reservation.css';
import table from '../../assets/images/table.jpg';


const Reservation = () => {


    const {
        register,
        formState: { errors, isValid },
        handleSubmit,
        reset,

    } = useForm({
        mode: 'onBlur',
    });

    const [submitted, setSubmitted] = useState(false);
    const [showSubmitButton, setShowSubmitButton] = useState(true);

    const onSubmit = (data) => {
        alert(JSON.stringify(data));
        setSubmitted(true);
        setShowSubmitButton(false);
        reset();
    }

    return (<section id='book'>
        <Container>
            <Row>
                <Col lg='6' md='6'>
                    <div className='form_img w-100'>
                        <img src={table} alt='' className='table_image w-100' />
                    </div>
                </Col>

                <Col lg='6' md='6'>
                    <div className='form_content'>
                        <h2>   !</h2>
                        <p>     .</p>
                    </div>
                    <div className='table_form'>
                        <form className='book_table row' onSubmit={handleSubmit(onSubmit)}>
                            <div className='form_group col-md-12'>

                                <input className='input_field' placeholder="  '" disabled={submitted} {...register('firstName', {
                                    required: '   `.',
                                    minLength: {
                                        value: 2,
                                        message: ' ,    .'
                                    }
                                })} />

                                <div className='error_msg'>
                                    {errors?.firstName && <p>{errors?.firstName.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12'>

                                <input className='input_field' placeholder="  " disabled={submitted} {...register('lastName', {
                                    required: '   `.',
                                    minLength: {
                                        value: 2,
                                        message: ' ,    .'
                                    }
                                })} />

                                <div className='error_msg'>
                                    {errors?.lastName && <p>{errors?.lastName.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12'>

                                <input className='input_field' placeholder="  Email" type='email' id='email' name='email' disabled={submitted} {...register('email', {
                                    required: '   `.',
                                    pattern: {
                                        value: /^\S+@\S+$/i,
                                        message: '    .'
                                    }
                                })} />

                                <div className='error_msg'>
                                    {errors?.email && <p>{errors?.email.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12'>

                                <input className='input_field' placeholder="   " type='tel' id='phoneNumber' name='phoneNumber' disabled={submitted} {...register('phone', {
                                    required: '   `.',
                                    minLength: {
                                        value: 6,
                                        message: ' ,   6 .'
                                    },
                                    maxLength: {
                                        value: 12,
                                        message: ' ,   12 .'
                                    }
                                })} />

                                <div className='error_msg'>
                                    {errors?.phone && <p>{errors?.phone.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12'>

                                <select className='input_field' disabled={submitted} label='select...' {...register("table", {
                                    required: ' ,   .'
                                })}>
                                    <option value="">  ...</option>
                                    <option value="1">1 </option>
                                    <option value="2">2 </option>
                                    <option value="3">3 </option>
                                    <option value="4">4 </option>
                                    <option value="6">6 </option>
                                    <option value="8">8 </option>
                                    <option value="10">10 </option>
                                </select>
                                <div className='error_msg'>
                                    {errors?.table && <p>{errors?.table.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12' >
                                <label className='l-radio'>
                                    <input className='radio_btn' type='radio' id='inside' value='Inside' disabled={submitted} {...register('position', {
                                        required: ' ,   .'
                                    })} /> 
                                </label>
                                <label className='l-radio'>
                                    <input className='radio_btn' type='radio' id='outside' value='Outside' disabled={submitted} {...register('position', {
                                        required: ' ,   .'
                                    })} /> 
                                </label>
                                <div className='error_msg'>
                                    {errors?.position && <p>{errors?.position.message || 'Error!'}</p>}
                                </div>
                            </div>

                            <div className='form_group col-md-12'>
                                {showSubmitButton && <input className='submit_btn' type='submit' value='' disabled={!isValid} />}
                            </div>
                        </form>
                        {submitted && <div><p>!<br />    ,    '.</p></div>}
                    </div>
                </Col>
            </Row>
        </Container>
    </section>
    )
}

export default Reservation;